body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body::-webkit-scrollbar {
  display: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* html, */
body {background-image: url("assets/background.jpg");
background-size: cover;
}




.glassCard{
  background-color: rgba(255, 255, 255, 0.1);
  /* fill-opacity: 50%; */

  backdrop-filter: blur(8px);
  padding: 2rem;
  border-radius: 10px;
  border-width: 1px;
  border-color: transparent;
  border-style: solid;
  outline-style: solid;
  outline-width: 1px;
  outline-color: rgba(255, 255, 255, 0.7);
  box-shadow: 0px 2px 15px 1px black;
  margin:20px;
}

/*  ☆*: .｡. o(≧▽≦)o .｡.:*☆  */
.maysCategory{
  background: linear-gradient(90deg, rgb(191, 0, 57), rgb(255, 255, 255) 100%);
  color: rgb(255, 255, 255);
  padding: 15px;
  max-width: fit-content;
  border-radius: 3rem;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Rubik', sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;
}

a:hover {
  text-decoration: underline;
}

header {
  background: var(--primary);
}

header a {
  color: var(--light);
}

button {
  background-color: rgba(255, 0, 132, 0.7);
  box-shadow:0px 0px 5px 5px rgb(0, 0, 0);
  color: white;
  font-size: 1.5rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  /* border-radius: .5rem; */
  border: none;
  outline: none;
  cursor: pointer;
  margin: 10px 5px;
}

button:hover {
  position: relative;
  top: 1px;
  /* left:1px; */
}

button[disabled] {
  cursor: default;
  opacity: .8;
}

.itemButton{
  color:rgb(0, 0, 0);
  border-radius: .5rem;
  color: black;
}

img {
  margin: 10px;
  max-width: 90%;
  border-radius: 8px;
}

span[role="img"] {
  cursor: default;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

.container-fluid {
  width: 100%;
  max-height:96vh;
  overflow-y: scroll;
  overflow-x: hidden;
  /* (┬┬﹏┬┬) */
}

.maysSuperItemContainer{
  display:flex;
  justify-content: center;
}
.container-fluid::-webkit-scrollbar {
  display: none;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}

/* .px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
} */

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

/* .py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
} */

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.error-text {
  color: var(--error);
}

@media screen and (max-width: 768px) {
  .card {
    width: 100%;
  }
}